  border-color: #eaeff4; }

.custom-control-input:disabled ~ .custom-control-label {
  opacity: .7; }
  .custom-control-input:disabled ~ .custom-control-label::before, .custom-control-input:disabled ~ .custom-control-label::after {
    opacity: 0.7; }

