  display: flex;
  align-items: center;
  font-weight: 300; }
  .custom-control-label::before {
    position: static;
    top: auto;
    border: 1px solid #eaeff4;
    margin-right: 0.5rem; }
  .custom-control-label::after {
    transform: scale(0);
    transition: transform .3s; }

