    transform: scale(1) rotate(35deg); }

.switch {
  position: relative;
  min-height: 1.9rem; }

.switch-inline {
  display: inline-flex;
  margin-right: 1rem; }

.switch-input {
  position: absolute;
  z-index: -1;
  opacity: 0; }
  .switch-input:checked ~ .switch-label::before {
    border-color: #bacad5; }
  .switch-input:checked ~ .switch-label::after {
    background: #084169;
    left: 24px; }
  .switch-input:disabled ~ .switch-label {
    opacity: .5; }

.switch-label {
  display: flex;
  align-items: center;
  font-weight: 300;
  margin-bottom: 0;
  cursor: pointer; }
  .switch-label::before {
    content: '';
    width: 40px;
    height: 20px;
    background: transparent;
    border: 2px solid #f1f2f3;
    border-radius: 20px;
    margin-right: 0.5rem;
    transition: 0.3s; }
  .switch-label::after {
    content: '';
    display: block;
    position: absolute;
    top: 6px;
    left: 4px;
    width: 12px;
    height: 12px;
    cursor: inherit;
    background: #e8e8e8;
    border-radius: 50%;
    transition: 0.3s; }

.switch-stacked .switch {
  display: inline; }
  .switch-stacked .switch::after {
    display: block;
    margin-bottom: .25rem;
    content: ""; }

