.text-truncate {
  width: auto;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis; }

.card-outline {
  background-color: transparent;
  border: 1px solid rgba(255, 255, 255, 0.3); }

.card-hover-inverse:hover {
  background-color: #191919;
  border-color: #191919; }

.text-white {
  color: rgba(255, 255, 255, 0.85) !important; }
  .text-white h1, .text-white h2, .text-white h3, .text-white h4, .text-white h5, .text-white h6,
  .text-white .h1, .text-white .h2, .text-white .h3, .text-white .h4, .text-white .h5, .text-white .h6 {
    color: #fff; }
  .text-white a:not(.btn):not(.dropdown-item) {
    color: rgba(255, 255, 255, 0.8); }
  .text-white hr {
    border-top-color: rgba(255, 255, 255, 0.15); }
  .text-white p {
    color: rgba(255, 255, 255, 0.85); }
  .text-white code {
    background-color: rgba(255, 255, 255, 0.1);
    color: #eee; }
  .text-white small,
  .text-white .small {
    color: rgba(255, 255, 255, 0.8); }
  .text-white .divider {
    color: rgba(255, 255, 255, 0.5); }
    .text-white .divider::before, .text-white .divider::after {
      border-top-color: rgba(255, 255, 255, 0.15); }
  .text-white .section-header small {
    color: rgba(255, 255, 255, 0.6); }
  .text-white .rating label.empty {
    color: rgba(255, 255, 255, 0.3); }
  .text-white .card-inverse {
    background-color: rgba(255, 255, 255, 0.1); }
  .text-white .btn-outline-light {
    color: rgba(255, 255, 255, 0.6);
    border-color: rgba(255, 255, 255, 0.3); }
    .text-white .btn-outline-light:hover {
      color: #757575; }
  .text-white .close {
    color: rgba(255, 255, 255, 0.85); }

.navbar-brand img {
  width: 150px; }

.feature-icon {
  max-width: 135px;
  min-height: 135px;
  margin: 0 auto;
  display: flex;
  align-items: center; }

.af-form {
  --primary-color: #084169;
  --border-focus-color: #084169;
  --font-family: "Open Sans", sans-serif;
  --label-font-weight: 600;
  --input-background: #fff; }

.af-form .af-legal a {
  text-decoration: underline; }
